<template>
	<div id="addcustomer">
		<!--顶部菜单-->
		<nav-bar class="addcustomer-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				新增单位
			</div>
			<div slot="right">
				<span @click="keepClick">保存</span>
			</div>
		</nav-bar>
		<!--滚动-->
		<scroll class="addcustomer-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<add-customer-content :base-info="baseInfo"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'

import AddCustomerContent from './childComps/AddCustomerContent'

import {SeedAddCustomerData} from 'network/customer'



export default {
  name: 'AddCustomer',
	components: {
		NavBar,
		Scroll,
		AddCustomerContent,
	},
	data(){
		return{
			btnName:'保存',
			baseInfo:{
				"unitcode":'',					//单位编号
				"unittext":'',					//单位名称
				"typeid":1,					//单位类别(例如：客户或者供应商)
				"unittype":'',					//单位类型(例如：开发部,人事部)
				"name":'',						//默认联系人
				"phone":'',						//电话
				"telephone":'',					//移动电话
				"location":'',					//地址
				"remarks":'',					//备注
				'email':'',						//电邮
				'postcode':'',					//邮编
				'website':'',					//公司网址
				'sex':0,						//性别
				'fax':''				//传真
			}
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//网络请求
		SeedAddCustomerData(obj){
			let _self=this
			SeedAddCustomerData(obj).then(res=>{				//这里是后台传出来的
				//订单操作完毕需要-去库存数量
				//console.log(res)
				this.$Toast.loading({
					message: '生成中...',
					forbidClick: true,
					onClose(){
						_self.$Toast(res)
						//成功后清空页面跳转
						_self.$router.push({ name: 'customersdetail', params: {id:res}})
					}
				});
				//新增一个出入库单的表
			})	
		},
		//保存按钮
		keepClick(){
			let baseInfo=this.baseInfo
			if(baseInfo.name==''){
				this.$Toast('名称不能为空')
				return false
			}else if(baseInfo.unittext==''){
				this.$Toast('单位名称不能为空')
				return false
			}else if(baseInfo.phone==''){
				this.$Toast('联系电话不能为空')
				return false
			}else if(baseInfo.location==''){
				this.$Toast('单位地址不能为空')
				return false
			}else if(baseInfo.email==''){
				this.$Toast('电子邮箱不能为空')
				return false
			}
			//console.log("保存按钮")
			//console.log(this.baseInfo)
			this.SeedAddCustomerData(baseInfo)
		}
	},
	activated(){
		this.baseInfo.unittype=this.$store.state.selectClassify.title
		//需要清空一下选中的类名
	}
}
</script >

