	background:#fff;
}
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }
#addcustomer{
	background:#f3f3f3;
    height: 100vh;
    position: relative;
	z-index:12;
}

.addcustomer-scroller{
    /*height:300px;*/
    position: relative;
	height:calc(100% - 46px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
